/* Nav */

.nav {
	padding: 20px 0;
	background-color: var(--nav-bg);
	border-bottom: 1px solid var(--nav-border);
	color: var(--nav-text);
	letter-spacing: normal;
}

.nav-row {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	column-gap: 30px;
	row-gap: 20px;
	flex-wrap: wrap;
}

.logo {
	margin-right: auto;
	color: var(--nav-text);
	font-size: 24px;
	font-family: 'Poppins', sans-serif;
}

.logo strong {
	font-weight: 700;
}

.nav-list {
	display: flex;
	flex-wrap: wrap;
	row-gap: 10px;
	align-items: center;
	column-gap: 40px;
	font-size: 16px;
	font-weight: 500;
	font-family: 'Poppins', sans-serif;
}

.nav-list__link {
	color: var(--nav-text);
	transition: opacity 0.3s ease-in-out;
	position: relative;
}

.nav-list__link:hover {
	opacity: 0.6;

	&::before {
		background-color: var(--accent);
		content: '';
		position: absolute;
		top: 100%;
		height: 2px;
		width: 100%;
	}
}

.nav-list__link--active {
	position: relative;

	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: 100%;
		display: block;
		height: 2px;
		width: 100%;
		background-color: var(--accent);
		transform-origin: right;
		transform: scaleX(1);
		transition: transform 0.3s ease-in-out;
	}

	&:hover::before {
		transform-origin: left;
		transform: scaleX(0);
	}
}